/**
 * @sillytavern/script
 */
export const extension_prompt_roles = {
  SYSTEM: 0,
  USER: 1,
  ASSISTANT: 2,
} as const;

/**
 * 
 */
export type GenerateConfig = {
  user_input?: string;
  image?: File | string | (File | string)[];
  should_stream?: boolean;
  overrides?: Overrides;
  injects?: InjectionPrompt[];
  max_chat_history?: 'all' | number;
};

/**
 * 
 */
export type GenerateRawConfig = {
  user_input?: string;
  image?: File | string | (File | string)[];
  should_stream?: boolean;
  overrides?: Overrides;
  injects?: InjectionRawPrompt[];
  ordered_prompts?: (BuiltinPrompt | RolePrompt)[];
  max_chat_history?: 'all' | number;
};

/**
 * 
 */
export type RolePrompt = {
  role: 'system' | 'assistant' | 'user';
  content: string;
  image?: File | string | (File | string)[];
};

/**
 * 
 */
export type InjectionPrompt = {
  role: 'system' | 'assistant' | 'user';
  content: string;
  position: 'before_prompt' | 'in_chat' | 'after_prompt' | 'none';
  depth: number;
  should_scan: boolean;
};

/**
 * 
 */
export type InjectionRawPrompt = {
  role: 'system' | 'assistant' | 'user';
  content: string;
  position: 'in_chat' | 'none';
  depth: number;
  should_scan: boolean;
};

/**
 * 
 */
export type Overrides = {
  world_info_before?: string; // ()
  persona_description?: string; // 
  char_description?: string; // 
  char_personality?: string; // 
  scenario?: string; // 
  world_info_after?: string; // ()
  dialogue_examples?: string; // 
  chat_history?: {
    with_depth_entries?: boolean;
    author_note?: string;
    prompts?: RolePrompt[];
  };
};

/**
 * 
 */
export type BuiltinPrompt =
  | 'world_info_before'
  | 'persona_description'
  | 'char_description'
  | 'char_personality'
  | 'scenario'
  | 'world_info_after'
  | 'dialogue_examples'
  | 'chat_history'
  | 'user_input';

/**
 * 
 */
export const builtin_prompt_default_order: BuiltinPrompt[] = [
  'world_info_before',
  'persona_description',
  'char_description',
  'char_personality',
  'scenario',
  'world_info_after',
  'dialogue_examples',
  'chat_history',
  'user_input',
];

/**
 * 
 */
export type BaseData = {
  characterInfo: {
    description: string;
    personality: string;
    persona: string;
    scenario: string;
    system: string;
    jailbreak: string;
  };
  chatContext: {
    oaiMessages: RolePrompt[];
    oaiMessageExamples: string[];
    promptBias: string[];
  };
  worldInfo: {
    worldInfoAfter: Array<string>;
    worldInfoBefore: Array<string>;
    worldInfoDepth: Array<{ entries: string; depth: number; role: number }>;
    worldInfoExamples: Array<string>;
    worldInfoString: Array<string>;
  };
};

/**
 * 
 */
export namespace detail {
  export type CustomPrompt = {
    role: 'system' | 'user' | 'assistant';
    content: string;
  };

  // 
  export type OverrideConfig = {
    world_info_before?: string; // 
    persona_description?: string; // 
    char_description?: string; // 
    char_personality?: string; // -
    scenario?: string; // 
    world_info_after?: string; // 
    dialogue_examples?: string; // -

    with_depth_entries?: boolean; // 
    author_note?: string; // 
    chat_history?: RolePrompt[]; // 
  };

  // 
  export type BuiltinPromptEntry =
    | 'world_info_before' // ()
    | 'persona_description' // 
    | 'char_description' // 
    | 'char_personality' // 
    | 'scenario' // 
    | 'world_info_after' // ()
    | 'dialogue_examples' // 
    | 'chat_history' // 
    | 'user_input'; // 

  // 
  export type GenerateParams = {
    user_input?: string;
    use_preset?: boolean;
    image?: File | string | (File | string)[];
    stream?: boolean;
    overrides?: OverrideConfig;
    max_chat_history?: number;
    inject?: InjectionPrompt[];
    order?: Array<BuiltinPromptEntry | CustomPrompt>;
  };
}

/**
 * 
 */
export const roleTypes: Record<
  'system' | 'user' | 'assistant',
  (typeof extension_prompt_roles)[keyof typeof extension_prompt_roles]
> = {
  system: extension_prompt_roles.SYSTEM,
  user: extension_prompt_roles.USER,
  assistant: extension_prompt_roles.ASSISTANT,
};

/**
 * 
 */
export const default_order: detail.BuiltinPromptEntry[] = [
  'world_info_before',
  'persona_description',
  'char_description',
  'char_personality',
  'scenario',
  'world_info_after',
  'dialogue_examples',
  'chat_history',
  'user_input',
];

/**
 * 
 */
export const character_names_behavior = {
  NONE: -1,
  DEFAULT: 0,
  COMPLETION: 1,
  CONTENT: 2,
};
