/**
 *  CSS  JS SillyTavernloadFileToDocument
 * CSS<style>JS<script>
 *
 * @param {string} url -  URL
 * @param {string} type - "CSS"  "JS"
 * @param {string} id -  ID
 */

export async function loadFileToHead(url: string, type: 'css' | 'js', id?: string) {
  return new Promise((resolve, reject) => {
    if (type === 'css') {
      fetch(url)
        .then(response => {
          if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
          }
          return response.text();
        })
        .then(cssContent => {
          if (id) {
            const existingStyle = document.getElementById(id);
            if (existingStyle) {
              existingStyle.remove();
            }
          }

          const styleElement = document.createElement('style');
          styleElement.textContent = cssContent;

          if (id) {
            const cleanId = id.toString().replace(/[^a-z0-9_-]/gi, '_');
            styleElement.id = cleanId;
          }

          document.head.appendChild(styleElement);
          resolve(styleElement);
        })
        .catch(error => {
          reject(error);
        });
    } else if (type === 'js') {
      const element = document.createElement('script');
      element.src = url;

      if (id) {
        const cleanId = id.toString().replace(/[^a-z0-9_-]/gi, '_');
        element.id = cleanId;
      }

      element.onload = resolve;
      element.onerror = reject;

      document.body.appendChild(element);
    } else {
      reject('Invalid type specified');
    }
  });
}
